import React ,{useState} from 'react';
import showAlert,{host} from '../lib';
import { Redirect, useLocation } from 'react-router-dom';

function ResetPass(){
    const location = useLocation();
    const [info,setInfo] = useState({
        username:location.state.username,
        password:'',
        repeatpass:''
    });

    const [isLogin, setLogin] = useState(false);
    function handleChange(e){
        const {name,value} = e.target;
        setInfo(prev => {
            return {
                ...prev,
                [name]:value
            }
        });
    }

    function handleClick(){
        const route = '/resetpassword';
        fetch(host+route,{
            method:'post',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Cache-Control': 'no-cache'
            },
            body: JSON.stringify(info)
        }).then(res => res.json()).then((data)=> {
            if(data==='OK'){
                showAlert("reset successfully");
                setLogin(true);
            } else {
                showAlert("reset failed");
                setInfo({
                    password:'',
                    repeatpass:'',
                })
            }
        })
    }

    if(isLogin){
        return <Redirect to='/' />
    }

    return (<div className='container'>
        <div className="py-5 text-center">
            <img className="d-block mx-auto mb-4" src="forgot_password.jpg" alt="" width="200" height="200" />
            <h2>Reset password here:</h2>
            <p className="lead">Type your new password.</p>
         </div>
         <div className="mb-3">
            <label htmlFor="password">What is your new password?</label>
            <input type="password" name='password' className="form-control" id="password" placeholder="Type here" value={info.password} onChange={handleChange} />
            <label htmlFor="repeatpass">Repeat password</label>
            <input type="password" name='repeatpass' className="form-control" id="repeatpass" placeholder="Type here" value={info.repeatpass} onChange={handleChange} />
        </div>
        <button type="button" className="btn btn-primary" onClick={handleClick}>Submit</button>
    </div>);
}

export default ResetPass;