import React from 'react';
import { Switch,Route  } from 'react-router-dom';
import Home from './Home'; 
import Account from './Account';
import AccountAdmin from './AccountAdmin';
import Login from './Login';
import SecondAuth from './SecondAuth';
import Register from './Register';
import Shop from './Shop';
import ForgotPass from './ForgotPassword';
import ResetPass from './ResetPassword';
import Register2Auth from './Register2Auth';

function LayoutChange(){
    return(
        <Switch>
            <Route path='/' exact children={<Home />}/>
            <Route path='/login' children={<Login />}/>
            <Route path='/secondauth' children={<SecondAuth />} />
            <Route path='/register' children={<Register />}/>
            <Route path='/register2auth' children={<Register2Auth /> } />
            <Route path='/account' children={<Account />}/>
            <Route path='/forgotpassword' children={<ForgotPass />} />
            <Route path='/resetpassword' children={<ResetPass /> } />
            <Route path='/admin' children={<AccountAdmin />}/>
            <Route path='/shop/:item' children={<Shop />}/>
        </Switch>
    );
}

export default LayoutChange;