import React, { useEffect } from 'react';
import {useLocation,Link} from 'react-router-dom';


function Register2Auth(){
    let location = useLocation();
    const qr = location.state.qr;

    const styleqr={
        width:'200px',
        marginLeft:'40%'
    }

    const button={
        marginLeft:'50%'
    }

    const text={
        textAlign:'center'
    }
    useEffect(()=>{
        const container = document.getElementById('qr');
        container.innerHTML = qr ;
    },[qr])



    return(<div>
    <p style={text}>Use google authenticator to scan this for next login</p>
    <div id="qr" style={styleqr}>
    </div>
    <Link type="button" style={button} className='btn btn-primary' to="/">Next</Link>
    </div>);
}

export default Register2Auth;