import React, {useState} from 'react';
import showAlert, {
  host
} from '../lib';
import { Redirect, useLocation} from 'react-router-dom';


function SecondAuth() {
  let location = useLocation();

  // the code input
  const [code, setCode] = useState(0);
  const layout ={
    padding:"0 35%"
  };

  const button ={
    marginTop:"20px"
  }

  // we have 3 state:
  // 0 : just load the page, not send info yet
  // 1 : sent info and authen successfully
  // -1: sent info and authen fail 
  const [isLogin, setLogin] = useState(0);

  function handleChange(e) {
    setCode(e.target.value);
  }

  function handleClick() {
      let info2Send = location.state.info;
      info2Send.code = code;
      const route = '/login';
      fetch(host + route, {
        method: 'post',
        mode: 'cors',
        credentials: 'include',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(info2Send)
      }).then(response => {
        if (response.status === 401) {
          showAlert("Wrong email or password");
          setLogin(-1);
        } else if (response.status === 200) {
          showAlert('Login success');
          setLogin(1);
        } else {
          showAlert("Login failed");
        }
      });
    }
  if(isLogin===1){
    return <Redirect to='/' />
  }
  if (isLogin===-1){
    return <Redirect to='/login' />
  }

  return (<div className="starter-template" style={layout}>
    <h1>Second authentication </h1> 
    <p className="lead"> Get the code in google Authenticator <br/> Put it in here </p> 
    <label htmlFor="code" className="sr-only">Your code here</label> 
    <input id="code" name="code" type="text" className="form-control" placeholder="000000" required="" value={ code } onChange={ handleChange } />
    <button style={button} type="button" className='btn btn-primary' onClick={ handleClick }> Submit </button> </div>);
}

  export default SecondAuth;