import React,{useState} from "react"; 
import showAlert,{host} from '../lib';
import {Redirect} from 'react-router-dom';

function ForgotPass(){
    const [info,setInfo] = useState({
        username:'',
        answer:''
    });

    const [isLogin,setLogin] = useState(false);

    function handleChange(e){
        const {name,value} = e.target;
        setInfo(prev => {
            return {
                ...prev,
                [name]:value
            };
        });
    }

    function handleClick(){
        const route = '/forgotpassword';
        fetch(host+route,{
            method:'post',
            headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
            'Cache-Control': 'no-cache'
            },
            body: JSON.stringify(info)
        }).then(res => res.json()).then((data)=> {
            if(data==='OK'){
                showAlert("Good answer");
                setLogin(true);
            } else {
                showAlert("failed");
                setInfo({
                    username:'',
                    answer:''
                })
            }
        })
    }

    if(isLogin){
        return <Redirect to={{
            pathname:'/resetpassword',
            state:{username:info.username}
        }} />
    }

    return(<div className="container">
        <div className="py-5 text-center">
            <img className="d-block mx-auto mb-4" src="forgot_password.jpg" alt="" width="200" height="200" />
            <h2>Please answer to reset password</h2>
            <p className="lead">Below is an KBA question, you have to answer to reset your password.</p>
         </div>
         <div className="mb-3">
            <label htmlFor="username">What is your email?</label>
            <input type="email" name='username' className="form-control" id="username" placeholder="Type here" value={info.username} onChange={handleChange} />
            <label htmlFor="answer">Where did you meet your spouse?</label>
            <input type="text" name='answer' className="form-control" id="answer" placeholder="Type here" value={info.answer} onChange={handleChange} />
        </div>
        <button type="button" className="btn btn-primary" onClick={handleClick}>Submit</button>
    </div>);
}

export default ForgotPass;